.answers{
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    padding: 20px;
    background-color: var(--pink);
    background-image: url(../../assets/img/answers-bg.png);
    background-repeat: no-repeat;
    background-size: 240px;
    background-position: calc(100% + 50px) 50px;
}

.answers__logo{
    margin-bottom: 20px;
}

.answers-list__answers{
    display: flex;
    grid-gap: 10px;
}

.final__title{
    margin-bottom: 15px;
}

.final__text{
    font-size: 22px;
    line-height: 1;
    font-weight: 700;
    margin-bottom: 30px;
}

.answer :global(input) {
    display: none;
}

.answer__image {
    max-width: 100%;
    object-fit: contain;
    margin-bottom: 20px;
}

.answer_type_pic :global(label),
.answer_type_background :global(label) {
    background-color: #fff;
    border-radius: 12px;
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    padding: 10px;
    border: 3px solid #000000;
    transition: .3s;
}

.answer_type_color :global(label) {
    display: flex;
    align-items: center;
    transition: .3s;
}

.answer__color {
    width: 70px;
    height: 70px;
    display: flex;
    border-radius: 35px;
    flex-shrink: 0;
    margin-right: 10px;
    border: 3px solid #fff;
    transition: .3s;
}

.answer__index {
    font-size: 15px;
    font-weight: 500;
}

.answer__title {
    font-size: 15px;
    font-weight: 500;
    text-align: left;
}

.answer__index {
    font-size: 15px;
    position: absolute;
    left: 10px;
    bottom: 10px;
}

.answers-list{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
}

.answers-list__item{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.answer-list__index{
    font-size: 22px;
    line-height: 1;
    font-weight: 700;
    margin-bottom: 10px;
}

.answer_type_pic :global(input:checked + label),
.answer_type_background :global(input:checked + label) {
    border-color: #000000;
}

.answer_type_color :global(input:checked + label) .answer__color {
    border-color: #000000;
}

.answer_type_color .answer__title {
    font-size: 24px;
}

.answer_type_background :global(label) {
    height: calc(100vw/2);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.answer_type_check :global(label) {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border: 3px solid #000000;
    border-radius: 12px;
}

.answer_type_check .answer__title {
    font-size: 20px;
    font-weight: 500;
}

.answer_type_check :global(label) {
    background-color: #000000;
}

.answer_type_check :global(label) .answer__title {
    color: #fff;
}

.answer_type_check{
    flex-direction: column;
}

.answer_type_background{
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    width: 100%;
}

.answers__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-gap: 10px;
    margin-top: 25px;
}

.answers__bottom-button{
    height: 55px;
    width: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 50%;
    flex-shrink: 0;
}