.question {
    padding: 40px 20px;
    box-sizing: border-box;
    min-height: 100dvh;
    display: flex;
    flex-direction: column;
}

.question-header{
    position: relative;
    margin-bottom: 25px;
}

.question-header__top{
    position: relative;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px;
}

.question-header__back-button{
    position: absolute;
    left: 0;
    top: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
}

.question-header__counter{
    font-size: 18px;
}

.question-header__progress{
    display: flex;
    grid-gap: 4px;
}

.question-header__progress-item{
    display: flex;
    height: 10px;
    width: 100%;
    background-color: #fff;
    border-radius: 5px;
    transition: .3s;
}

.question-header__progress-item_active{
    background: var(--green);
}

.question__title {
    margin: 0;
    text-align: left;
    margin-bottom: 40px;
}

.question__body {
    display: grid;
}

.question__body_grid_3 {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
}

.question__body_grid_2 {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
}


.question__body_grid_1 {
    grid-gap: 10px;
}


.answer {}

.answer :global(input) {
    display: none;
}

.answer__image {
    max-width: 100%;
    object-fit: contain;
    margin-bottom: 20px;
}

.answer_type_pic :global(label),
.answer_type_background :global(label) {
    background-color: #fff;
    border-radius: 12px;
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    padding: 10px;
    border: 3px solid #ffffff;
    transition: .3s;
}

.answer_type_color :global(label) {
    display: flex;
    align-items: center;
    transition: .3s;
}

.answer__color {
    width: 70px;
    height: 70px;
    display: flex;
    border-radius: 35px;
    flex-shrink: 0;
    margin-right: 10px;
    border: 3px solid #fff;
    transition: .3s;
}

.answer__index {
    font-size: 15px;
    font-weight: 500;
}

.answer__title {
    font-size: 15px;
    font-weight: 500;
    text-align: left;
}

.answer__index {
    font-size: 15px;
    position: absolute;
    left: 10px;
    bottom: 10px;
}

.answer_type_pic :global(input:checked + label),
.answer_type_background :global(input:checked + label) {
    border-color: #000000;
}

.answer_type_color :global(input:checked + label) .answer__color {
    border-color: #000000;
}

.answer_type_color .answer__title {
    font-size: 24px;
}

.answer_type_background :global(label) {
    height: calc(100vw/2);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.question__bottom {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.answer_type_check :global(label) {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border: 3px solid #000000;
    border-radius: 12px;
}

.answer_type_check .answer__title {
    font-size: 20px;
    font-weight: 500;
}

.answer_type_check :global(input:checked + label) {
    background-color: #000000;
}

.answer_type_check :global(input:checked + label) .answer__title {
    color: #fff;
}