.onboarding-start{
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    padding: 20px;
    background-image: url(../../assets/img/loading-bg.png);
    background-color: var(--pink);
    background-size: 500px;
    background-position: center 40px;
    background-repeat: no-repeat;
}

.onboarding-start__logo{
    margin-right: auto;
    margin-bottom: 20px;
}

.onboarding-start__buttons{
    display: flex;
    flex-direction: column;
    margin-top: auto;
    grid-gap: 25px;
    padding: 0 20px 20px;
}
