.onboarding-middle {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    width: 100vw;
}

.onboarding-middle__pic-wrapper {
    background-color: var(--pink);
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 50dvh;
    overflow: hidden;
}

.onboarding-middle__pic {
    /* width: 350px; */
    height: 100%;
    width: auto;
    max-width: calc(100% - 20px);
}

.onboarding-middle__content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    min-height: 50dvh;
}

.onboarding-middle__logo {
    margin-bottom: 20px;
}

.onboarding-middle__text {
    margin-bottom: 25px;
}

.onboarding-middle__button {
    margin-top: auto;
    flex-shrink: 0;
}