@font-face {
  font-family: "Graphik LCG";
  src: url("./assets/fonts/Graphik-Bold-Web/Graphik-Bold-Web.eot");
  src: url("./assets/fonts/Graphik-Bold-Web/Graphik-Bold-Web.eot?#iefix")format("embedded-opentype"),
      url("./assets/fonts/Graphik-Bold-Web/Graphik-Bold-Web.woff")format("woff"),
      url("./assets/fonts/Graphik-Bold-Web/Graphik-Bold-Web.woff2")format("woff2"),
      url("./assets/fonts/Graphik-Bold-Web/Graphik-Bold-Web.ttf")format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Graphik LCG";
  src: url("./assets/fonts/Graphik-Medium-Web/Graphik-Medium-Web.eot");
  src: url("./assets/fonts/Graphik-Medium-Web/Graphik-Medium-Web.eot?#iefix")format("embedded-opentype"),
      url("./assets/fonts/Graphik-Medium-Web/Graphik-Medium-Web.woff")format("woff"),
      url("./assets/fonts/Graphik-Medium-Web/Graphik-Medium-Web.woff2")format("woff2"),
      url("./assets/fonts/Graphik-Medium-Web/Graphik-Medium-Web.ttf")format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

body {
  margin: 0;
  font-family: "Graphik LCG", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  outline: none !important;
}

img {
  -drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}


ul,
ol,
p,
div,
label,
h1,
h2,
h3 {
  list-style: none;
  padding: 0;
  margin: 0;
}

button,
button span {
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

a,
a:hover {
  text-decoration: none;
}


:root {
  --white: #FFFFFF;
  --black: #000000;
  --green: #DBFF00;
  --pink: #FED1FA;
}

* {
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  background-color: #F1F1F1;
}