.final {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    width: 100vw;
}

.final__pic-wrapper {
    background-color: var(--pink);
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 50dvh;
    overflow: hidden;
}

.final__pic {
    /* width: 350px; */
    max-height: 100%;
    width: auto;
    max-width: calc(100% - 20px);
}

.final__content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    min-height: 50dvh;
}

.final__logo {
    margin-bottom: 20px;
}

.final__title {
    margin-bottom: 20px;
}

.final__text-first {
    margin-bottom: 25px;
}

.final__text-second {
    margin-top: 20px;
    margin-bottom: 20px;
}

.final__text-third {
    margin-top: 15px;
    margin-bottom: 20px;
}

.final__button {
    margin-top: auto;
    flex-shrink: 0;
}

.copy-block{
    display: flex;
    align-items: center;
}

.copy-block__ticket{
    width: 230px;
    height: 36px;
    background-image: url(../../assets/img/ticket.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
}