.button {
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 28px;
    /* width: max-content; */
    width: 100%;
    text-transform: uppercase;
    padding: 0 24px;
    border: none;
    box-shadow: 0 6px 6px rgba(0, 0, 0, .25);

    font-size: 17px;
    font-weight: 500;
}

.button :global(span) {
    font-size: 14px;
    font-weight: 500;
}

.button_color_white {
    background: var(--white);
    color: var(--black);
}

.button_color_black {
    background: #000;
    color: #fff;
}

.button_color_green {
    background: var(--green);
    color: var(--black);
}